<template>
  <div
        class="keyboard"
      >
        <vbutton
          color="blue"
          size="s1"
          :label="rotation"
          arrow="translate(0, 33px)"
          :position="true"
          :active="keyboard['rotate']"
          ref="dom_rotate"
          class="rotation-btn"
        />
        <vbutton
          color="blue"
          size="s1"

          :label="labelDown"
          arrow="translate(0,-36px) rotate(180deg)"
          :active="keyboard['down']"
          ref="dom_down"
          class="down-btn"
        />
        <vbutton
          color="blue"
          size="s1"
          :label="labelLeft"
          arrow="translate(30px, -6px) rotate(270deg)"
          :active="keyboard['left']"
          ref="dom_left"
          class="left-btn"
        />
        <vbutton
          color="blue"
          size="s1"
          :label="labelRight"
          arrow="translate(-30px, -6px) rotate(90deg)"
          :active="keyboard['right']"
          ref="dom_right"
          class="right-btn"
        />
        <vbutton
          color="blue"
          size="s0"
          :label="labelDropSpace"
          :active="keyboard['drop']"
          ref="dom_space"
          class="drop-btn"
        />
        <vbutton
          color="red"
          size="s2"
          :label="labelResetR"
          :active="keyboard['reset']"
          ref="dom_r"
          class="reset-btn"
        />
        <vbutton
          color="green"
          size="s2"
          :label="labelSoundS"
          :active="keyboard['music']"
          ref="dom_s"
          class="sound-btn"
        />
        <vbutton
          color="green"
          size="s2"
          :label="labelPauseP"
          :active="keyboard['pause']"
          ref="dom_p"
          class="pause-btn"
        />
      </div>
</template>

<script >
    import Vbutton from './button/index.vue'
    import { i18n, lan } from '../../unit/const'
    import store from '../../vuex/store'
    import todo from '../../control/todo'

    export default {
        props: ['filling'],
        data() {
            return {
                fillingNum: 0
            }
        },
        watch: {
            $props: {
                deep: true,
                handler(nextProps) {
                    this.fillingNum = nextProps.filling + 20
                }
            }
        },
        computed: {
            keyboard() {
                return this.$store.state.keyboard
            },
            rotation: () => i18n.rotation[lan],
            labelLeft: () => i18n.left[lan],
            labelRight: () => i18n.right[lan],
            labelDown: () => i18n.down[lan],
            labelDropSpace: () => `${i18n.drop[lan]} (SPACE)`,
            labelResetR: () => `${i18n.reset[lan]}(R)`,
            labelSoundS: () => `${i18n.sound[lan]}(S)`,
            labelPauseP: () => `${i18n.pause[lan]}(P)`
        },
        mounted() {
            const touchEventCatch = {} // , touchstart, , mouse

            // mousedown, mouseup, , mouseoutmouseup
            const mouseDownEventCatch = {}
            document.addEventListener(
                'touchstart',
                e => {
                    if (e.preventDefault) {
                        e.preventDefault()
                    }
                },
                { passive: false }
            )
            document.addEventListener('touchend', (e) => {
                if (e.preventDefault) {
                    e.preventDefault();
                }
            }, { passive: false });

            // 
            document.addEventListener('gesturestart', (event) => {
                event.preventDefault();
            });

            document.addEventListener(
                'mousedown',
                e => {
                    if (e.preventDefault) {
                        e.preventDefault()
                    }
                },
                true
            )
            Object.keys(todo).forEach(key => {
                this.$refs[`dom_${key}`].$el.addEventListener(
                    'mousedown',
                    () => {
                        if (touchEventCatch[key] === true) {
                            return
                        }
                        todo[key].down(store)
                        mouseDownEventCatch[key] = true
                    },
                    true
                )
                this.$refs[`dom_${key}`].$el.addEventListener(
                    'mouseup',
                    () => {
                        if (touchEventCatch[key] === true) {
                            touchEventCatch[key] = false
                            return
                        }
                        todo[key].up(store)
                        mouseDownEventCatch[key] = false
                    },
                    true
                )
                this.$refs[`dom_${key}`].$el.addEventListener(
                    'mouseout',
                    () => {
                        if (mouseDownEventCatch[key] === true) {
                            todo[key].up(store)
                        }
                    },
                    true
                )
                this.$refs[`dom_${key}`].$el.addEventListener(
                    'touchstart',
                    () => {
                        touchEventCatch[key] = true
                        todo[key].down(store)
                    },
                    true
                )
                this.$refs[`dom_${key}`].$el.addEventListener(
                    'touchend',
                    () => {
                        todo[key].up(store)
                    },
                    true
                )
            })
        },
        components: {
            Vbutton
        }
    }

</script>

