<template>
    <div class="game-page-box tetris-page-box">
        <div class="app" :style="size">
            <div class="rect" :class="drop?'drop':''">
                <Decorate/>
                <div class="screen">
                    <div class="panel">
                        <Matrix :propMatrix="matrix" :cur="cur" :reset="reset" />
                        <Logo :cur="!!cur" :reset="reset" />
                        <div class="state">
                            <Point :cur="!!cur" :max="max" :point="points" />
                            <p>{{pContent}}</p>
                            <Number :number='cur ? clearLines : startLines' />
                            <p>{{level}}</p>
                            <Number :number='cur?speedRun:speedStart' :length="1" />
                            <p>{{nextText}}</p>
                            <Next :data="next" />
                            <div class="bottom">
                                <Music :data="music" />
                                <Pause :data="pause" />
                                <Number :propTime="true" />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <Keyboard :filling='filling' />
        </div>
    </div>
</template>

<script>

    import { mapState } from 'vuex'
    import { transform, lastRecord, speeds, i18n, lan } from './unit/const'
    import { visibilityChangeEvent, isFocus } from './unit/'
    import states from './control/states'
    import {sdk,browser} from "../../common/util/bitUtil";

    import store from './vuex/store'
    import './unit/const';
    import './control';
    import { subscribeRecord } from './unit';
    subscribeRecord(store); // localStorage

    import Decorate from './components/decorate/index.vue'
    import Guide from './components/guide/index.vue'
    import Next from './components/next/index.vue'
    import Music from './components/music/index.vue'
    import Pause from './components/pause/index.vue'
    import Number from './components/number/index.vue'
    import Point from './components/point/index.vue'
    import Keyboard from './components/keyboard/index.vue'
    import Logo from './components/logo/index.vue'
    import Matrix from './components/matrix/index.vue'

    export default {
        name: 'tetris',
        store: store,
        data() {
            return {
                size: {},
                w: document.documentElement.clientWidth,
                h: document.documentElement.clientHeight,
                filling: ''
            }
        },
        components: {
            Decorate,
            Guide,
            Next,
            Music,
            Pause,
            Number,
            Point,
            Logo,
            Keyboard,
            Matrix
        },
        computed: {
            pContent() {
                return this.cur ? i18n.cleans[lan] : i18n.startLine[lan]
            },
            level: () => i18n.level[lan],
            nextText: () => i18n.next[lan],
            ...mapState([
                'matrix',
                'keyboard',
                'music',
                'pause',
                'next',
                'cur',
                'speedStart',
                'speedRun',
                'startLines',
                'clearLines',
                'points',
                'max',
                'reset',
                'drop'
            ])
        },
        methods: {
            render() {
                let filling = 0
                const size = (() => {
                    const w = this.w
                    const h = this.h
                    const ratio = h / w
                    let scale
                    let css = {}
                    // if (ratio < 1.5) {
                    //     scale = h / 960
                    // } else {
                    //     scale = w / 640
                    //     filling = (h - 960 * scale) / scale / 3
                    //     css = {
                    //         'padding-top': Math.floor(filling) + 42 + 'px',
                    //         'padding-bottom': Math.floor(filling) + 'px',
                    //         'margin-top': Math.floor(-480 - filling * 1.5) + 'px'
                    //     }
                    // }
                    // css[transform] = `scale(${scale})`
                    console.log(css)
                    return css
                })()
                this.size = size
                this.start()
                this.filling = filling
            },
            resize() {
                this.w = document.documentElement.clientWidth
                this.h = document.documentElement.clientHeight
                this.render()
            },
            start() {
                if (visibilityChangeEvent) {
                    // store
                    document.addEventListener(
                        visibilityChangeEvent,
                        () => {
                            states.focus(isFocus())
                        },
                        false
                    )
                }

                if (lastRecord) {
                    // 
                    if (lastRecord.cur && !lastRecord.pause) {
                        // , , 
                        const speedRun = this.$store.state.speedRun
                        let timeout = speeds[speedRun - 1] / 2 // , 
                        // 
                        timeout =
                            speedRun < speeds[speeds.length - 1]
                                ? speeds[speeds.length - 1]
                                : speedRun
                        states.auto(timeout)
                    }

                    if (!lastRecord.cur) {
                        states.overStart()
                    }
                } else {
                    states.overStart()
                }
            }
        },
        mounted() {
            let query = this.$route.query || {}
            const gameInstance = states
            if(browser.app){
                sdk.onAdCompletion(function(params){
                    console.log(params)
                })

                gameInstance.hooks.start = function () {
                    sdk.gameShowAd(query)
                }
            }
            this.render()
            window.addEventListener('resize', this.resize.bind(this), true)
        },
    }
</script>
